.svg-wrap {
  position: absolute;
  z-index: 4;
  width: 32px;
  height: 32px;
}

svg * {
  pointer-events: none;
}
.nav-link:hover .svg-wrap {
  /* transition: transform 1s; */
  transform: scale(1.5);
  transform-origin: center;
  transform-box: fill-box;
}
.nav-link:hover #head {
  transform-origin: center;
  transform-box: fill-box;
  -webkit-animation: nodHead 0.5s ease-in infinite alternate;
          animation: nodHead 0.5s ease-in infinite alternate;
}
.nav-link:hover #tie {
  -webkit-animation: knot 1s ease-in-out infinite alternate;
          animation: knot 1s ease-in-out infinite alternate;
  transform-origin: center;
  transform-box: fill-box;
}
.nav-link:hover #arrow-left {
  -webkit-animation: arrowMoveLeft 1s ease-in-out infinite alternate;
          animation: arrowMoveLeft 1s ease-in-out infinite alternate;
  transform-origin: center;
  transform-box: fill-box;
}
.nav-link:hover #arrow-right {
  -webkit-animation: arrowMoveRight 1s ease-in-out infinite alternate;
          animation: arrowMoveRight 1s ease-in-out infinite alternate;
  transform-origin: center;
  transform-box: fill-box;
}
.nav-link:hover #sun {
  fill: rgb(121, 119, 119);
  -webkit-animation: sunRotate 2s 0.4s ease-in-out infinite;
          animation: sunRotate 2s 0.4s ease-in-out infinite;
  transform-origin: bottom;
}
.nav-link:hover #moon {
  fill: rgb(121, 119, 119);
  -webkit-animation: sunRotate 2s 1.4s ease-in-out infinite;
          animation: sunRotate 2s 1.4s ease-in-out infinite;
  transform-origin: bottom;
}
.nav-link:hover .letter {
  -webkit-animation: letterOut 1s ease-in-out infinite alternate;
          animation: letterOut 1s ease-in-out infinite alternate;
  transform-origin: center;
  transform-box: border-box;
}

@-webkit-keyframes nodHead {
  from {
    transform: rotateX(0deg) translateY(0px);
    stroke: currentColor;
  }
  to {
    transform: rotateX(30deg) translateY(60px);
    stroke: rgb(121, 119, 119);
  }
}

@keyframes nodHead {
  from {
    transform: rotateX(0deg) translateY(0px);
    stroke: currentColor;
  }
  to {
    transform: rotateX(30deg) translateY(60px);
    stroke: rgb(121, 119, 119);
  }
}
@-webkit-keyframes knot {
  from {
    transform: rotateY(0deg);
    fill: currentColor;
  }
  to {
    transform: rotateY(60deg);
    fill: rgb(121, 119, 119);
  }
}
@keyframes knot {
  from {
    transform: rotateY(0deg);
    fill: currentColor;
  }
  to {
    transform: rotateY(60deg);
    fill: rgb(121, 119, 119);
  }
}
@-webkit-keyframes arrowMoveLeft {
  from {
    transform: translateX(0);
    fill: currentColor;
  }
  to {
    transform: translateX(-30px);
    fill: rgb(121, 119, 119);
  }
}
@keyframes arrowMoveLeft {
  from {
    transform: translateX(0);
    fill: currentColor;
  }
  to {
    transform: translateX(-30px);
    fill: rgb(121, 119, 119);
  }
}
@-webkit-keyframes arrowMoveRight {
  from {
    transform: translateX(0);
    fill: currentColor;
  }
  to {
    transform: translateX(30px);
    fill: rgb(121, 119, 119);
  }
}
@keyframes arrowMoveRight {
  from {
    transform: translateX(0);
    fill: currentColor;
  }
  to {
    transform: translateX(30px);
    fill: rgb(121, 119, 119);
  }
}
@-webkit-keyframes sunRotate {
  0% {
    transform: rotateZ(0deg);
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
  50% {
    transform: rotateZ(180deg);
  }
  70% {
    transform: rotateZ(350deg);
    opacity: 0;
  }
  100% {
    transform: rotateZ(360deg);
    opacity: 1;
  }
}
@keyframes sunRotate {
  0% {
    transform: rotateZ(0deg);
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
  50% {
    transform: rotateZ(180deg);
  }
  70% {
    transform: rotateZ(350deg);
    opacity: 0;
  }
  100% {
    transform: rotateZ(360deg);
    opacity: 1;
  }
}
@-webkit-keyframes letterOut {
  from {
    transform: translateY(0);
    fill: currentColor;
  }
  to {
    transform: translateY(-21px);
    fill: rgb(121, 119, 119);
  }
}
@keyframes letterOut {
  from {
    transform: translateY(0);
    fill: currentColor;
  }
  to {
    transform: translateY(-21px);
    fill: rgb(121, 119, 119);
  }
}
/*card*/
.card-wrap {
  width: 28%;
  height: 50vh;
  text-align: center;
  border: 3px solid black;
  border-radius: 9px;
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  overflow: hidden;
  margin-top: 5%;
  transition: transform 0.4s ease-in-out;
}
.card-image {
  background-color: grey;
  width: 100%;
  height: 65%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.card-wrap:hover {
  transform: scale(1.1);
}
.card-wrap:hover > .card-text {
  transform: scale(0.9);
}
.card-text {
  width: 100%;
  height: 30%;
  font-size: 1em;
  padding: 2em;
  color: white;
  background-color: darkolivegreen;
  transition: transform 0.4s ease-in-out;
  border-radius: 0px 0px 9px 9px;
}
.card-text:hover {
  color: black;
}
/*progress bar*/
.skill-wrap {
  width: 100%;
  /* border-radius: 9px; */
  /* padding: 5%; */
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-content: space-between;
}
.skill-card {
  background-color: #086586;
  background-color: var(--main-color);
  color: #fff;
  color: var(--second-color);
