/*timeline*/
.timeline-wrap {
  position: relative;
  background-color: rgb(32, 32, 32);
  height: 633.6px;
  width: 356.4px;
  transform: rotate(-90deg) translateX(-356.4px);
  transform-origin: left top;
  overflow-y: scroll;
  overflow-x: hidden;
  scrollbar-width: none;
  -ms-overflow-style: none;
  /*   
  background-color: rgb(96, 96, 114);
  width: 100%;
  height: 18vh; */
}

.timeline-inner-wrap {
  display: flex;
  flex-direction: row;
  width: 600vw;
  transform: rotate(90deg) translateY(-356.4px);
  transform-origin: left top;
  text-align: center;
}
.timeline-unit-box {
  background-color: grey;
  height: 356.4px;
  width: 633.6px;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto auto;
  grid-gap: 10px;
}
.timeline-bar {
  grid-row-start: 1;
  margin: 30% 0%;
  background-color: red;
}
/*timeline progress bar*/
.timeline-progress-bar-wrap{
  display: flex;
  padding: 9px;
  width: 500px;
  height: 50px;
  background-color: grey;
}
.timeline-progress-bar{
  background-color: red;
}
/*EndBar*/
.endbar {
  margin-top: 50%;
  width: 100%;
  height: 50px;
  background-color: red;
}
.endbar-done {
  margin-top: 10%;
  width: 100%;
  height: 50px;
  background-color: green;
}
/*element for contact page*/
.contactMsg {
  width: 100%;
  text-align: left;
  font-size: 36px;
  line-height: 42px;
  color: #fff;
  color: var(--second-color);
}
.copyBar {
  text-align: center;
  border-radius: 15px;
  background-color: #3b3838;
  background-color: var(--third-color);
  padding: 9px;
  margin: 36px auto;
  width: 100%;
  line-height: 64px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  display: flex;
  position: relative;
}
.copyLabel {
  height: 16px;
  padding: 0px 6px;
  background: #3b3838;
  background: var(--third-color);
  color: #fff;
  color: var(--second-color);
  line-height: 16px;
  position: absolute;
  left: 36px;
  top: 3px;
  transition: 0.3s;
}
.copyText {
  outline: none;
  padding: 12px 0px;
  margin-right: 3px;
  background: none;
  color: #fff;
  color: var(--second-color);
  border: 1px solid #fff;
  border: 1px solid var(--second-color);
  border-radius: 12px 3px 3px 12px;
  font-size: 36px;
  text-align: center;
  width: 90%;
  transition: 0.3s;
}
.copyText:focus {
  border: 3px solid #086586;
  border: 3px solid var(--main-color);
  padding: 10px;
}
.copyText:focus ~ .copyLabel {
  color: #086586;
  color: var(--main-color);
}
.copyButton {
  background-color: #fff;
  background-color: var(--second-color);
  color: #3b3838;
  color: var(--third-color);
  border: 0;
  border-radius: 3px 12px 12px 3px;
  width: 10%;
  transition: ease-in-out 0.3s;
  overflow: hidden;
}
.copyButton:hover {
  background-color: #086586;
  background-color: var(--main-color);
  color: #fff;
  color: var(--second-color);
}
.clipboard {
  transform: scale(0.9);
  /* transition: 0.3s; */
}
.clipboard.checked {
  transform-origin: 15px 19.2px;
  transform: scale(5.4);
}
/*line-breaker*/
p.min-break {
  height: 36px;
  width: 100%;
}

div#aboutme {
  box-shadow: inset 0 0 6px rgb(0, 0, 0);
